.carouselItem {
    background-color: #f7f7f7;
    border: 1px solid #f8f8f8;
    box-shadow: 0 3px 3px #c4c4c4;
    display: flex;
    flex-direction: column;
    height: auto;
    margin-inline: 10px;
    min-height: 400px;
    transition: all 0.3s ease-in-out;
    width: 300px;
    gap: 15px;
  }
  .carouselItem:hover {
    box-shadow: 0 3px 15px #c4c4c4;
    -webkit-transform: scale(1.05);
    transform: scale(1.05);
  }
  .carouselItemImage {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .container {
    width: 80%;
    max-height: max-content;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    font-family: sans-serif;
    row-gap: 50px;
  }
  
  .techList {
    position: relative;
    width: auto;
    height: auto;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
  }
  
  .techItem {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 35px;
    height: 35px;
    border-radius: 100px;
    box-shadow: 0 2px 5px #ccc;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 0;
    background-color: #fff;
  }
  
  .techItemImage {
    width: 20px;
    height: auto;
  }
  
  .footer {
    align-items: center;
    color: #ef3948;
    cursor: pointer;
    display: flex;
    flex-direction: row;
  }
  .footer1 {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0 12px 15px;
    width: calc(100% - 0px);
    margin: auto;
  }
  
  .textdata {
    text-align: left;
    margin-left: 4px;
  }
  .textdata > h3 {
    color: #333;
    font-size: 24px;
    margin-bottom: 10px;
  }
  .textdata > p {
    color: #555;
    font-size: 16px;
  }
  .projecttitile {
    width: 75%;
    text-align: left;
    margin: auto;
    margin-bottom: 25px;
    margin-top: 40px;
    line-height: normal;
  }
  .projecttitile h1 {
    
        font-size: 28px;
        color: #4070f4;
        font-weight: 700;
        margin-bottom: 3.5rem;
        text-align: left;
        
    
  }
  
  @media screen and (max-width: 1300px) {
    .container{
      width: 85%;
    }
    .projecttitile{
      width: 80%;
    }
    .carouselItem{
      width: 400px;
    }
  }
  
  @media screen and (max-width: 1100px) {
    .container{
      width: 90%;
    }
    .projecttitile{
      width: 80%;
    }
    .carouselItem{
      width: 400px;
    }
  }
  
  @media screen and (max-width: 900px) {
    .container{
      width: 95%;
    }
    .projecttitile{
      width: 90%;
    }
    .carouselItem{
      width: 450px;
    }
  }
  
  @media screen and (max-width: 700px) {
    .container{
      width: 100%;
    }
    .projecttitile{
      width: 95%;
    }
    .carouselItem{
      width: 400px;
    }
  }
  